<template>
	<view class="pages">
		<view class="content">
			<view class="labeltop">
				<image
					src="data:image/png;base64,"
					class="logoimg"></image>
				<view class="title">欢迎登录卓微科技</view>
			</view>

			<view class="form">
				<view class="formboxsed">
					<view class="formboxs">
						<uni-forms ref="form">
							<view class="formitem">
								<view class="item divflex">
									<uni-easyinput type="" v-model="Account" placeholder="请输入您的账号" />
								</view>
							</view>
							<view class="formitem">
								<view class="item divflex">
									<uni-easyinput type="password" v-model="Password" placeholder="请输入密码" />
								</view>
							</view>
							<u-button @click="submit" class="btnli btn-login" :ripple="true" ripple-bg-color="#6c8aac">登
								录</u-button>
						</uni-forms>
					</view>
				</view>
				<view class="agreement">
					<text class="tet">注册即代表您同意</text>
					<text @click="openPage('/pages/content/index?code=MEMBER')" class="tet blue">《卓微科技用户协议》</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		getLogin
	} from '@/api/api.js'
	export default {
		data() {
			return {
				Account: '',
				Password: '',
				second: '60',
				is_second: false,
				timer: null
			};
		},
		onLoad() {

		},


		onload() {
			uni.setStorageSync('first_falg', true);
		},
		onShow() {

		},
		methods: {
			submit() {
				if (this.Account && this.Password) {
					const params = {
						Account: this.Account,
						Password: this.Password,
						Strategy: "app",
						IdentType: 1
					};
					// console.log(params.Password,111)
					getLogin(params).then(res => {
						if (res.statusCode === 200) {
							uni.setStorageSync("token", res.data.token);
							uni.setStorageSync("account", this.Account);
							uni.switchTab({
								url: '/pages/index/index'
							});

						}
						if (res.statusCode == 0) {
							uni.showModal({
								title: "密码或账号错误"
							})
						}
					});
				} else {
					if (this.Account === "" && this.Password === "") {
						uni.showModal({
							title: "账号或密码不能为空",
							textAlign: "center",
						})
					}

				}



			},


			openPage(url, type) {
				if (type == 'reLaunch') {
					uni.reLaunch({
						url: url
					})
				} else {
					uni.navigateTo({
						url: url
					})
				}
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import "index.scss";
</style>
